import notifier from './notifier';
import fetchCountries from './fetchCountries';
import markup from './markup';
const debounce = require('lodash.debounce');

class UiService {
  constructor() {
    this.query = '';
    this.isCountryClicked = false;
  }
}
const ui = new UiService();

function onQueryInput(event) {
  ui.query = event.target.value;
  fetchCountries(ui.query).then(onFetchSucces).catch(onFetchError);
}

function onCountryItemClick(e) {
  e.preventDefault();
  if (e.target.nodeName !== 'A') return;

  ui.isCountryClicked = true;
  ui.query = e.target.innerText;
  fetchCountries(ui.query).then(onFetchSucces).catch(onFetchError);
}

function onFetchSucces(data) {
  const countriesList = data.map(item => {
    return { name: item.name.common, flag: item.flags.svg };
  });
  markup.resetMarkup();
  if (countriesList.length > 10) {
    notifier.onLongListAlert();
    return;
  }

  if (countriesList.length === 1) {
    const preperedObject = {};
    const countryData = data[0];
    preperedObject.name = countryData.name.common;
    preperedObject.capital = countryData.capital[0];
    preperedObject.langueges = Object.values(countryData.languages);
    preperedObject.population = countryData.population;
    preperedObject.flag = countryData.flags.svg;
    console.log(preperedObject);

    markup.renderCountryCard(preperedObject);
    return;
  }

  if (
    data.filter(item => item.name === ui.query).length === 1 &&
    ui.isCountryClicked
  ) {
    const matchedCoutry = data.filter(item => item.name === ui.query);
    markup.renderCountryCard(matchedCoutry[0]);
    ui.isCountryClicked = false;
    return;
  }

  console.log(countriesList);

  markup.renderCountries(countriesList);
}

function onFetchError(e) {
  notifier.onError();
}

export default function initUi() {
  markup.refs.searchInput.addEventListener(
    'input',
    debounce(onQueryInput, 500),
  );
  markup.refs.countriesContainer.addEventListener('click', onCountryItemClick);
}
